import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import iconInternet from './../img/ico-internet.svg';
import iconTv from './../img/ico-tv.svg';
import iconSale from './../img/ico-sale.svg';




const HeaderServiceListItem = (props) => {
  return (
    <li className="header__service-item">
      <Link to={props.link} className="header__service-link">
        <img src={props.img} className="header__service-img" />
        <span className="header__service-name">
          {props.name}
        </span>
      </Link>
    </li>
  )
}

class HeaderServiceList extends Component {


  constructor(props) {
    super(props);
    this.state = {
      serviceList: [
        {
          link: '#',
          img: iconInternet,
          name: 'Интернет'
        },
        {
          link: '#',
          img: iconTv,
          name: 'Телебачення'
        },
        {
          link: '#',
          img: iconSale,
          name: 'Акціі'
        },
      ]
    }
  }

  render() {

    return (
      <div className="header__service">
        <ul className="header__service-list">
          {
            this.state.serviceList.map((s) => {
              return (
                <HeaderServiceListItem 
                key={s.name}
                link={s.link}
                img={s.img}
                name={s.name}
                />
              )
            })
          }
        </ul>
      </div>
    )
  }
}

export default HeaderServiceList;