import React, { Component } from 'react';
import { Link } from 'react-router-dom';




const ToolsListItem = (props) => {
  return (
    <li className="tools__list-item">
      <Link className="tools__list-link" to={props.link}>
        <img src={props.img} className="tools__list-img" />
        <span className="tools__list-name">
          {props.name}
        </span>
      </Link>
    </li>
  )
}



export default ToolsListItem;