import React, { Component } from 'react';
import './Tools.scss';
import perevagi from './../../img/1.svg';
import tarifi from './../../img/2.svg';
import pidcluch from './../../img/3.svg';
import oplata from './../../img/4.svg';
import ToolsListItem from './ToolsListItem';





class Tools extends Component {


  constructor(props) {
    super(props);
    this.state = {
      toolsList: [
        {
          name: 'ПЕРЕВАГИ МЕРЕЖІ',
          link: '#',
          img: perevagi
        },
        {
          name: 'ТАРИФИ НА ПОСЛУГИ',
          link: '#',
          img: tarifi
        },
        {
          name: 'МОЖЛИВІСТЬ ПІДКЛЮЧЕННЯ',
          link: '#',
          img: pidcluch
        },
        {
          name: 'СПОСОБИ ОПЛАТИ',
          link: '#',
          img: oplata
        },
      ]
    }
  }

  render() {

    return (
      <div className="tools">
        <ul className="tools__list">
        {this.state.toolsList.map((toolsList) => {
          return (
            <ToolsListItem
            key={toolsList.name}
            name={toolsList.name}
            link={toolsList.link}
            img={toolsList.img}
          />
          )
        })}
        </ul>
      </div>
    )
  }
}


export default Tools;
