import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './BurgerMenu.scss';


const Menu = (props) => (
  <li className="nav__list-item">
    <Link to={props.link} onClick={props.cl} className="nav__list-link">
      {props.name}
    </Link>
    {props.children}
  </li>
)


const List2 = (props) => (
  <li className="nav__item-under">
    <Link to={props.linklist2} className="nav__link-under">
      {props.linkname}
    </Link>
  </li>
)


const list1 = [
  {
    name: 'Интернет',
    className: 'nav-item zanpacto',
    listname2: [
      {
        name2: 'Тарифи(Квартири)',
        link: '#'
      },
      {
        name2: 'Тарифи(Приватний сектор)',
        link: '#'
      },
      {
        name2: 'Тарифи для бизнесу',
        link: '#'
      },
      {
        name2: 'Акційні тарифи',
        link: '#'
      },
      {
        name2: 'Додаткові послуги',
        link: '#'
      }
    ]
  },
  {
    name: 'Телебачення',
    className: 'nav-item pers',
    listname2: [
      {
        name2: 'Omega TV',
        link: '#'
      },
      {
        name2: 'Trinity TV',
        link: '#'
      }
    ]
  },
  {
    name: 'Новини',
    className: 'nav-item',
    link: '#'
  },
  {
    name: 'Про нас',
    className: 'nav-item',
    link: '#'
  },
  {
    name: 'Вакансії',
    className: 'nav-item',
    link: '#'
  },
  {
    name: 'Підртримка',
    className: 'nav-item',
    link: '#'
  }
]



class BurgerMenu extends Component {
  constructor(props) {
    super(props);
    this.state = {

      isToggleOn: false,
      list2ClassName: 'nav-list-under',
      outerListStatuses: list1.reduce((res, { name }) => ({ ...res, [name]: false }), {})
    };
  }

  toggleOuterListItem = (name) => {
    this.setState(state => ({
      outerListStatuses: {
        ...state.outerListStatuses,
        [name]: !state.outerListStatuses[name]
      }
    }));
  };

  handleClick = () => {
    this.setState(state => ({
      isToggleOn: !state.isToggleOn
    }));
  }



  render() {




    const {
      isToggleOn,
      outerListStatuses,
    } = this.state


    return (
      <div className="nav">
        <ul className={this.state.isToggleOn ? 'nav__list' : 'nav__list active'}>
          {list1.map((list1) => {
            return (
              <Menu
                key={list1.name}
                name={list1.name}
                cl={() => this.toggleOuterListItem(
                  list1.name
                )}
              >
                { list1.listname2 &&
                  <ul className={`nav__list-under ${outerListStatuses[list1.name] ? 'active' : ''}`}>
                    {list1.listname2.map((list2) => {
                      return (
                        <List2 key={list2.name2} linklist2={list2.link} linkname={list2.name2} className={list2.listName2} />
                      )
                    })}
                  </ul>
                }
              </Menu>
            )
          })}
        </ul>
        <button aria-label="burger-menu" className={this.state.isToggleOn ? 'burger' : 'burger active'} onClick={this.handleClick}>
          <span className={this.state.isToggleOn ? 'burger-span1' : 'burger-span1 active'}></span>
          <span className={this.state.isToggleOn ? 'burger-span2' : 'burger-span2 active'}></span>
          <span className={this.state.isToggleOn ? 'burger-span3' : 'burger-span3 active'}></span>
        </button>
      </div>
    );
  }
}

export default BurgerMenu;